*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
header{
  height: 90%;
  background: url(https://images.unsplash.com/photo-1518932945647-7a1c969f8be2?ixid=MXwxMjA3fDB8MHxzZWFyY2h8MzF8fGNvZGluZ3xlbnwwfHwwfA%3D%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=400&q=60) no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
}
header::after{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5)
} 
.container{
  max-width: 1000px;
  margin: 0 auto;
}
nav{
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #ffffff;
  position: relative;
  z-index: 10;
}
ul.menu{
  display: flex;
  list-style: none;
}
ul.menu li{
  margin: 0 1rem;
}
ul.menu li a{
  text-decoration: none;
  color: #ffffff;
  border-bottom: 2px solid transparent;
  transition: 0.3s;
}
ul.menu li a:hover{
  border-bottom: 2px solid #ffffff;
  padding-bottom: 0.5rem;
}

.header-wrapper{
  display: flex;
  align-items: center;
  height: 500px;
  margin-top: 2rem;
  position: relative;
  z-index: 10;
}
.header-info{
  color: #ffffff;
}
.header-info h1{
  font-size: 5rem;
}
.header-info p{
  font-size: 1.5rem;
  color: #000000;
  background: #ffffff;
  padding: 0.5rem;
}